<template>
	<el-button type="primary" plain id="jokebtn" @click='getJoke'>获取笑话</el-button>
	<el-button type="primary" plain @click='getPhone'>获取归属地</el-button>
</template>

<script>
	import axios from 'axios'
	export default {
		name: 'joke',
		data: function() {
			return {
				ajoke: '',
				num: 15012312312,
				myKey: '11aa8a90c58f6e9b401d77ee6d2f96bf',
			}
		},
		methods: {
			tips(msg) {
				this.$notify({
					title: '笑话',
					message: msg,
					duration: 0
				});
			},
			getJoke() {
				axios.get('https://autumnfish.cn/api/joke')
					.then(resp => {
						this.tips(resp.data)
					})
					.catch()
			},
			getPhone() {
				//http://apis.juhe.cn/mobile/get?phone=13429667914&key=您申请的KEY
				axios.get('http://apis.juhe.cn/mobile/get?phone=13429667914&key=11aa8a90c58f6e9b401d77ee6d2f96bf')
					.then(resp => {
						console.log(resp)
					})
					.catch()
			}
		}
	}
</script>

<style>
	#jokebtn {
		position: fixed;
		right: 0;
		bottom: 0;
	}
</style>
